import { authService } from "fbase";

export const authStateChanged = (setLoggedIn, setUserObj, setInit) => {
    authService.onAuthStateChanged((user) => {
        if (user) {
            //     user setLoggedIn  . =>    boolean .
            setLoggedIn(true);
            const { uid, displayName } = user;
            //  state props       ,   .
            setUserObj({
                uid,
                displayName,
                updateProfile: (args) => user.updateProfile(args),
            }); //    .
        } else {
            //    false  .
            setLoggedIn(false);
        }
        // setInit true  Initializing... .
        setInit(true); //  useEffect  setInit true.
    });
};

export const logout = () => {
    // firebase auth    .
    // firebase.auth().signOut();
    authService.signOut();
};
