import { HashRouter, Route, Routes } from "react-router-dom";
import { Home, Auth, Profile } from "routes";
import Navigator from "components/Navigator";
import styled from "styled-components";

const Router = ({ isLoggedIn, userObj, refreshUser }) => {
    // useState   =>  
    // isLoggedIn     !

    return (
        <HashRouter>
            {isLoggedIn && <Navigator userObj={userObj} />}
            <View
                children={
                    <Routes>
                        {!isLoggedIn ? (
                            // React-Router-Dom v6 Redirect   Route path *.
                            <Route path="/" element={<Auth />} />
                        ) : (
                            <>
                                <Route
                                    path="/"
                                    element={<Home userObj={userObj} />}
                                />
                                <Route
                                    path="/profile"
                                    element={
                                        <Profile
                                            userObj={userObj}
                                            refreshUser={refreshUser}
                                        />
                                    }
                                />
                                {/* <Route
                                    path="/profile/:id"
                                    element={<Profile />}
                                /> */}
                            </>
                        )}
                    </Routes>
                }
            />
        </HashRouter>
    );
};

const View = ({ children }) => {
    return <WrapView>{children}</WrapView>;
};

const WrapView = styled.div`
    width: 90%;
    padding: 1%;
`;

export default Router;
