import { useState } from "react";
import { authService } from "fbase";

const AuthForm = () => {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [newAccount, setNewAccount] = useState(true);
    const [error, setError] = useState(""); //    .
    const handlerChange = (event) => {
        // React    state  .
        const { name, value } = event.target; //   name, value
        if (name === "email") {
            setEmail(value);
        } else if (name === "password") {
            setPassword(value);
        }
    };

    const handlerLogin = async (event) => {
        // event.target id email, password DOM 
        event.preventDefault(); //    
        try {
            let data;
            const email = event.target.email.value;
            const password = event.target.password.value;
            if (newAccount) {
                // create newAccount
                //  6 : FirebaseError: Firebase: Password should be at least 6 characters (auth/weak-password).
                //      : Firebase: The email address is already in use by another account. (auth/email-already-in-use).
                await authService.createUserWithEmailAndPassword(
                    email,
                    password
                );
            } else {
                // sign-in()
                await authService.signInWithEmailAndPassword(email, password);
            }
        } catch (err) {
            setError(err.message);
        }
    };

    const handlerToggle = () => {
        // Not  true -> false, false -> true .
        setNewAccount(!newAccount);
    };

    return (
        <>
            <form onSubmit={handlerLogin}>
                <h1 style={{ fontSize: "30px", fontWeight: "bold" }}>Login</h1>
                <div>
                    <input
                        type="text"
                        name="email"
                        id="email"
                        placeholder="email"
                        value={email}
                        onChange={handlerChange}
                    />
                    <input
                        type="password"
                        name="password"
                        id="password"
                        placeholder="password"
                        value={password}
                        onChange={handlerChange}
                    />
                    <input
                        type="submit"
                        value={newAccount ? "Create Account" : "Login"}
                    />
                </div>
                {error && <p>{error}</p>}
            </form>
            <span onClick={handlerToggle}>
                {newAccount ? "Sign in" : "Create Account"}
            </span>
        </>
    );
};

export default AuthForm;
