import { useRef, useState } from "react";
import { v4 as uuidv4 } from "uuid"; // firebase storage   id    uuid .
import { storageService } from "fbase";
import { DatabaseService } from "service/NweetsFactory";

const NweetFactory = ({ userObj: { uid, displayName } }) => {
    const [nweet, setNweet] = useState(""); // nweet
    const [attachment, setAttachment] = useState(""); //  

    const fileRef = useRef();

    const handlerSubmit = async (event) => {
        event.preventDefault();
        //      . ( )
        // firebase.firestore().collection().add promise .
        // firebase.storage().ref().child   ,    . (base64)
        let data = { nweet, uid, displayName, attachmentUrl: "" };
        if (attachment) {
            //  
            const uuid = uuidv4();
            const attachmentRef = storageService.ref().child(`${uid}/${uuid}`);
            const response = await attachmentRef.putString(
                attachment,
                "data_url"
            );

            // storage   
            const attachmentUrl = await response.ref.getDownloadURL(); // image url
            data = { ...data, attachmentUrl: attachmentUrl };
        }
        await DatabaseService.addNweet(data);

        initState();
    };

    const handlerChangeNweet = (event) => {
        // jsx input value , key    .
        //  onChange     event.target.value  useState       .
        setNweet(event.target.value);
    };

    // image file change
    const handlerFileChange = (event) => {
        // event.target.files :   .
        const { files } = event.target;
        [...files].forEach((file) => {
            const reader = new FileReader();
            //       
            reader.onloadend = (event) => {
                // event.isTrusted :      
                const {
                    currentTarget: { result },
                } = event;
                //   base64 .
                setAttachment(result);
            };
            // File  
            reader.readAsDataURL(file);
        });
    };

    //   
    const handlerImageClear = () => {
        if (attachment) {
            setAttachment(""); //  .
            // input file 
            fileRef.current.value = "";
        }
    };

    const initState = () => {
        //  
        setNweet("");
        setAttachment("");
    };

    return (
        <div>
            <form onSubmit={handlerSubmit}>
                <div>
                    <input
                        type="text"
                        value={nweet}
                        onChange={handlerChangeNweet} // change   .
                        placeholder="What's on your mind?" // hint
                        maxLength={120} //  120 .
                    />
                    {/* input file type :  
                    accept      .
                 */}
                    <input
                        type="file"
                        ref={fileRef}
                        name="uploadImage"
                        id="uploadImage"
                        accept="image/*"
                        onChange={handlerFileChange}
                    />
                </div>
                {/* src   base64    */}
                <div>
                    <img
                        src={attachment}
                        alt=" "
                        width="100"
                        height="100"
                    />
                    <button type="button" onClick={handlerImageClear}>
                        clear
                    </button>
                </div>
                <input type="submit" value="Nweet" />
            </form>
        </div>
    );
};

export default NweetFactory;
