import { dbService } from "fbase";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import Nweet from "components/Nweet";
import * as AuthService from "service/AuthService";

const Profile = ({ userObj, refreshUser }) => {
    const navigate = useNavigate();
    const [myNweet, setMyNweet] = useState([]);
    const [displayName, setDisplayName] = useState(userObj.displayName);
    useEffect(() => {
        //    
        getMyNweet();
    }, []);

    const getMyNweet = async () => {
        const { uid } = userObj;
        await dbService
            .collection("nweets")
            .where("uid", "==", uid) // (key, condition, value)
            .onSnapshot((snapshot) => {
                snapshot.docs.forEach((document) => {
                    setMyNweet((prev) => [
                        ...prev,
                        { ...document.data(), id: document.id },
                    ]);
                });
            });
    };

    const handlerLogout = () => {
        if (window.confirm("   ? ")) {
            AuthService.logout();
            // Login 
            navigate("/");
        }
    };

    const handlerDisplayChange = (event) => {
        const {
            target: { value },
        } = event;
        setDisplayName(value);
    };

    const updateProfile = async (event) => {
        event.preventDefault();

        if (userObj.displayName === displayName) {
            return;
        }
        //    userObj.updateProfile({ displayName, photoURL })
        // await userObj.updateProfile  undefined
        await userObj.updateProfile({ displayName });
        refreshUser(); //   .
    };

    // Nweets .
    const liNweets = myNweet.map(
        ({ text, createAt, id, uid, attachmentUrl, displayName }) => {
            return (
                <Nweet
                    key={id}
                    nweetObj={{
                        text,
                        createAt,
                        id,
                        uid,
                        attachmentUrl,
                        displayName,
                    }}
                    isOwner={userObj.uid === uid}
                />
            );
        }
    );

    return (
        <div>
            Profile
            <div>
                <form id="form" onSubmit={updateProfile}>
                    <input
                        type="text"
                        name="displayName"
                        id="displayName"
                        value={displayName}
                        onChange={handlerDisplayChange}
                    />
                    <input type="submit" value="Update Submit" />
                </form>
            </div>
            <button onClick={handlerLogout}>Logout</button>
            {liNweets}
        </div>
    );
};

export default Profile;
