import { dbService } from "fbase";

export const addNweet = ({ nweet, uid, attachmentUrl, displayName }) => {
    dbService.collection("nweets").add({
        text: nweet,
        createAt: Date.now(), //    .
        uid, //  uid
        attachmentUrl, // image base64  .
        displayName,
    });
};

export const findNweets = (collection, callback) => {
    // onSnapshot firebase    .
    //     .
    // snapshot.docs firestore    ...
    dbService
        .collection(collection)
        .orderBy("createAt", "desc") // firebase        .
        .onSnapshot((snapshot) => callback(snapshot));
};
